* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
}

.content {
  max-width: 1366px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
}
a {
  text-decoration: none;
  list-style: none;
}




/* ХЭДЕР */
header {
  width: 100%;
  height: 120px;
  background: rgb(8, 8, 8);
  position: sticky;
  top: 0;
  z-index: 100;
}

header p {
  color: #fff;
}

.header_wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
}

.logo_block {
  width: 200px;
  height: 100%;
  background: rgb(8, 8, 8);
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;
  z-index: 10;
}

.logo_img {
  width: 100px;
  height: 100px;
  background: url('../src/img/logo.jpg');
}


.logo_block p {
  font-size: 26px;
}
.logo_block p::first-letter {
  color: red;
}
nav {
  width: 600px;
  height: 50px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
ul {
  width: 100%;
  height: 100%;
  text-decoration: none;
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
header li {
  color: #fff;
  font-size: 20px;
  cursor: pointer;
  transition: 1s;
  margin-left: 20px;
}
header li::first-letter {
  color: red;
}
header li:hover {
  transform: scale(1.1);
}
#cart_img {
  width: 50px;
  height: 50px;
  background: url('../src/img/cart.jpg');
  animation: swing 5s linear infinite;  
  cursor: pointer;
  z-index: 10;
}




/* ОСНОВНОЙ КОНТЕНТ */
.section_wrapper {
  width: 100%;
  height: 100%;
}

.p_name_page {
  font-size: 36px;
  margin-top: 50px;
}

.p_name_page::first-letter {
  color: red;
}

.wrapper_eat {
  box-shadow: 2px 4px 38px 0px rgba(34, 60, 80, 0.2);
  width: 100%;
  height: 100%;
  background: #fff;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  flex-direction: row;
  border-radius: 30px;
}

.eat_block {
  width: 300px;
  height: 400px;
  background: #fff;
  border-radius: 20px;
  box-shadow: 2px 4px 38px 0px rgba(34, 60, 80, 0.2);
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 20px;
  transition: 1s;
  cursor: pointer;
}

.eat_block img {
  width: 300px;
  height: 200px;
}

.eat_block:hover {
  transform: scale(0.9);
}

.title {
  font-size: 18px;
  font-weight: bold;
}


.par {
  width: 80%;
  margin: 0 auto;
  text-align: center;
}

.add {
  height: 100px;
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

  .price {
      font-size: 18px;
      font-weight: bold;
  }

  .buy {
    width: 30px;
    height: 30px;
    background: rgb(62, 255, 56);
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 34px;
    transition: .3s;
    cursor: pointer;
  }

  .buy:hover {
    background: #fff;
    color: rgb(62, 255, 56);
    border-radius: 100%;
  }

  .par {
    text-align: center;
    color: rgb(116, 116, 116);
    margin-top: 15px;
  }

  .counter {
    border: 3px solid rgb(255, 227, 150);
    border-radius: 30px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-size: 24px;
    width: 120px;
    height: 40px;
  }

  .plus, .minus {
    height: 30px;
    width: 30px;
    background: red;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgb(255, 196, 32);
    border-radius: 50%;
    color: #fff;
    transition: .3s;
    cursor: pointer;
  }

  .plus:hover, .minus:hover {
    background: #fff;
    color: rgb(255, 196, 32);
  }









  /* КОРЗИНА */
  #cart_click {
    background: rgba(0,0,0, 0.8);
    position: fixed;
    width: 100%;
    z-index: 199;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    /* display: none; */
  }

  .update {
    background: red;
    width: 100px;
    color: #fff;
    font-size: 18px;
    text-align: center;
    cursor: pointer;
  }
  .cart_block {
    width: 400px;
    height: 100vh;
    background: #fff;
    position: absolute;
    right: 0;
    animation: cartOpen;
    animation-duration: .5s;
    animation-fill-mode: forwards;
    
  }

  @keyframes cartOpen {
    0% {
    -webkit-transform: translateX(100%);
    transform: translateX(100%);
    visibility: visible;
    }
    100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
    }
    } 


  #order_name {
    text-align: center;
    font-size: 30px;
  }

  #order_name::first-letter, .price_order::first-letter, .order_block_name::first-letter, #total::first-letter {
    color: red;
  }

  .order_block {
    width: 100%;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    border-bottom: 1px solid rgb(255, 227, 150);
  }

  .order_block .order_block_name {
    font-size: 20px;
  }
  .order_block .counter {
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-size: 14px;
    width: 120px;
    height: 25px;
    border: none;
  }

  .order_block .plus, .order_block .minus {
    height: 22px;
    width: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgb(255, 196, 32);
    border-radius: 50%;
    font-size: 24px;
    color: #fff;
    transition: .3s;
    cursor: pointer;
  }

  .order_block  .plus:hover, .minus:hover {
    background: #fff;
    color: rgb(255, 196, 32);
  }

.price_order {
  font-size: 20px;
}

#close {
  position: absolute;
  bottom: 0;
  width: 200%;
  height: 20px;
  background: red;
  display: flex;
  align-items: center;
  padding: 0 0 0 200px;
  font-size: 18px;
  font-weight: bold;
  color: #fff;
  cursor: pointer;
  transition: .3s;
}
#close:hover {
  background: #fff;
  color: red;
}
#total {
  position: relative;
}
#sum {
  font-size: 22px;
  margin-left: 10px;
  color: rgb(40, 197, 1);
}

#order_go {
  background: rgb(40, 197, 1);
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: .3s;
}
#order_go:hover {
  color: rgb(40, 197, 1);
  background: #fff;
}
.del {
  color: #fff;
  background: red;
  font-weight: bold;
  width: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}








/* МОДАЛЬНЫЕ ОКНА */
.modal_click {
  background: rgba(0,0,0, 0.8);
  position: fixed;
  width: 100%;
  z-index: 199;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  max-width: 450px;
  width: 100%;
  height: 500px;
  background: #fff;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
#close_modal {
  width: 100%;
  height: 20px;
  background: red;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  font-weight: bold;
  color: #fff;
  cursor: pointer;
  transition: .3s;
}
#close_modal:hover {
  background: #fff;
  color: red;
}
.lozung {
  text-align: center;
  font-size: 20px;
}




/* ОТЗЫВЫ */
form {
  width: 100%;
  height: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
textarea {
  width: 90%;
  height: 90%;
}
input {
  width: 30%;
  margin-top: 10px;
  margin-bottom: 10px;
}
.button {
  border: none;
  background: red;
  color: #fff;
  width: 150px;
  height: 50px;
  margin-top: 20px;
  font-size: 16px;
  border-radius: 20px;
  cursor: pointer;
  transition: .3s;
  display: flex;
  justify-content: center;
  align-items: center;
}
.button:hover {
  color: red;
  background: #fff;
  border: 3px solid red
}
.post_block {
  max-width: 520px;
  border-radius: 30px;
  width: 100%;
  height: 100%;
  border: 3px solid red;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  margin: 0 auto;
  margin-top: 50px;
  padding: 6px 6px 6px 6px;
  text-align: center;
}

.like {
  width: 50px;
  height: 30px;
  margin-left: 20px;
  background: rgb(0, 162, 255);
  cursor: pointer;
  color: #fff;
}
.like:hover {
  color: rgb(0, 162, 255);
  background: #fff;
  border: none
}











/* ГЛАВНАЯ */
h1,h2 {
  text-align: center;
  margin-top: 50px;
}
h1::first-letter, h2::first-letter{
  color: red;
}

#g_map {
  margin-top: 50px;
  border: none;
}




















/* СТИЛЬ ПОЛОСЫ + АНИМКИ */
#hr1 {
  border: none;
  height: 3px;
  margin: 40px 0;
  background-image: linear-gradient(to right, #F000, #000, #fff);
}
#hr1:before,
#hr1:after {
  content: '';
  display: inline-block;
  border-radius: 100%;
  width: 40px;
  height: 40px;
  border: 2px solid red;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #FFF;
}
#hr1:after {
  animation: anim-hr 1s linear infinite;  
}

@keyframes anim-hr {
  0% {
      width: 40px;
      height: 40px;
  } 
  100% {
      width: 0;
      height: 0;
  }
}


@keyframes swing {
  0%{
      transform: scale(1);
  }
  59%{
      transform: scale(1);
  }
  60% {
  -webkit-transform: rotate3d(0, 0, 1, 15deg);
  transform: rotate3d(0, 0, 1, 15deg);
  }
  70% {
  -webkit-transform: rotate3d(0, 0, 1, -10deg);
  transform: rotate3d(0, 0, 1, -10deg);
  }
  80% {
  -webkit-transform: rotate3d(0, 0, 1, 5deg);
  transform: rotate3d(0, 0, 1, 5deg);
  }
  90% {
  -webkit-transform: rotate3d(0, 0, 1, -5deg);
  transform: rotate3d(0, 0, 1, -5deg);
  }
  100% {
  -webkit-transform: rotate3d(0, 0, 1, 0deg);
  transform: rotate3d(0, 0, 1, 0deg);
  }
  }